import { Injectable } from '@angular/core';
import { Subject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class LoadingActionService {

  loading = new Subject<any>();
  listerning$ = this.loading.asObservable();

  constructor() { }

  open() {
    this.loading.next(true);
  }

  off() {
    this.loading.next(false);
  }

}
