import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { Router, NavigationStart, ActivatedRoute } from '@angular/router';
import { AuthService } from './core/services/auth.service';
import { LoadingActionService } from './core/services/loading-action.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent implements OnInit {
  @ViewChild('addClassEle') addClassEle: ElementRef;

  title = 'new-angular-training';
  toggleLoading = false;

  addClass = false;

  tabId = 1;

  constructor(
    private router: Router,
    private auth: AuthService,
    private loading: LoadingActionService
  ) {

  }

  toggle(evt: any, asd: string) {
    this.addClass = !this.addClass;
  }

  ngOnInit() {
    this.router.events.subscribe((data) => {
      if (data instanceof NavigationStart) {
        window.scrollTo(0, 0);
        this.auth.checkLogged();
      }
    });

    // this.router.navigate(['/events']);

    this.loading.listerning$.subscribe(data => {
      console.log(data);
      this.toggleLoading = data;
    });
  }

  send(id: number) {
    this.tabId = id;
  }

  move() {
    // this.router.navigateByUrl('/feature');
    this.router.navigate(['/feature'], {queryParams: {abc: 'dong'}});
  }

}
