import { Component, OnInit, OnDestroy, Input } from '@angular/core';
import { AuthService } from './../../../core/services/auth.service';
import { ComponentInterationService } from './../../../core/services/component-interation.service';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss']
})
export class HeaderComponent implements OnInit, OnDestroy {

  @Input() notFixed: boolean;
  sub = new Subscription();

  constructor(
    public auth: AuthService,
    private iteraction: ComponentInterationService
  ) { }

  ngOnInit() {
    // console.log(this.auth.isLogin);
    this.sub.add(this.iteraction.$getSource.subscribe((data) => console.log(data)));
    this.sub.add(this.iteraction.$getSource.subscribe((data) => console.log(data)));
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
  }

  logout(): void {
    this.auth.logout();
  }

}
