import { Component, OnInit, Input, OnChanges, SimpleChanges, Output, EventEmitter, OnDestroy } from '@angular/core';
import { ComponentInterationService } from './../../../core/services/component-interation.service';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-dialog',
  templateUrl: './dialog.component.html',
  styleUrls: ['./dialog.component.scss']
})
export class DialogComponent implements OnInit, OnChanges, OnDestroy {

  public toogleDlg = false;
  sub = new Subscription();

  @Output() afterClose = new EventEmitter();

  @Input('toggle')
  set toggle(event: boolean) {
    this.toogleDlg = event;
  }
  get toogle() {
    return this.toogleDlg;
  }

  constructor(
    private _interactor: ComponentInterationService
  ) { }

  ngOnInit() {
    this.sub.add(this._interactor.$getSource.subscribe((data) => {
      this.toogleDlg = data;
      console.log(data);
    }));
  }

  ngOnChanges(changes: SimpleChanges) {
    // this.toggle = true;
  }

  close(data: any) {
    this.toogleDlg = false;
    this.afterClose.emit(data);
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
  }

}
