import { NgModule } from '@angular/core';
import { Routes, RouterModule, PreloadAllModules, NoPreloading } from '@angular/router';

import { FeaturesComponent } from './features/features.component';
import { AccountGuard } from './account/account.guard';
import { SelectivePreloadingStrategyService } from './core/services/selective-preloading-strategy.service';
import { NotFoundComponent } from './not-found/not-found.component';
import { TabContentComponent } from './tab-content/tab-content.component';
import { FeatureComponent } from './feature/feature.component';
import { AppComponent } from './app.component';
import { OtherComponent } from './other/other.component';
import { UserComponent } from './user/user.component';
import { UserGuard } from './user/user.guard';
import { OtherLoginComponent } from './other-login/other-login.component';
import { UserProfileComponent } from './user/user-profile/user-profile.component';
import { UserDashboardComponent } from './user/user-dashboard/user-dashboard.component';
import { UserCanDeactivate } from './user/user.deactive-guard';
import { DashboardResolver } from './user/user-dashboard/user.resolver';
import { ClientComponent } from './client/client.component';
import { ClientGuard } from './client/client.guard';
import { NewLoginComponent } from './new-login/new-login.component';
import { ClientHomeComponent } from './client/client-home/client-home.component';
import { ClientProfileComponent } from './client/client-profile/client-profile.component';
import { CanDeactivateClientProfile } from './client/client-profile/client-profile.candeactive';
import { ClientResolver } from './client/client-profile/client-profile.resolve';

const routes: Routes = [
  {
    path: '',
    component: AppComponent
  },
  {
    path: 'feature',
    component: FeatureComponent
  },
  {
    path: 'other',
    component: OtherComponent
  },
  {
    path: 'other-login',
    component: OtherLoginComponent
  },
  {
    path: 'user',
    component: UserComponent,
    canActivate: [UserGuard],
    canActivateChild: [UserGuard],
    children: [
      {
        path: 'profile',
        component: UserProfileComponent,
        canDeactivate: [UserCanDeactivate]
      },
      {
        path: 'dashboard',
        component: UserDashboardComponent,
        resolve: {
          user: DashboardResolver
        }
      }
    ]
  },
  {
    path: 'client',
    component: ClientComponent,
    canActivate: [ClientGuard],
    canActivateChild: [ClientGuard],
    children: [
      {
        path: ':id',
        component: ClientHomeComponent
      },
      {
        path: 'profile',
        component: ClientProfileComponent,
        canDeactivate: [CanDeactivateClientProfile],
        resolve: {
          userData: ClientResolver
        }
      }
    ]
  },
  // {
  //   path: 'account',
  //   loadChildren: './account/account.module#AccountModule',
  //   // canLoad: [AccountGuard],
  //   data: { preload: true }
  // }
  {
    path: 'api-working',
    loadChildren: () => import('./api-working/api-working.module').then(m => m.ApiWorkingModule)
  },
  {
    path: 'lazy',
    loadChildren: () => import('./demo-lazy/demo-lazy.module').then(m => m.DemoLazyModule)
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes, {preloadingStrategy: SelectivePreloadingStrategyService})],
  exports: [RouterModule]
})
export class AppRoutingModule { }
