import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { observable, Observable, of } from 'rxjs';
import { CookiesService } from './cookies.service';
import { LoadingActionService } from './loading-action.service';

@Injectable({
  providedIn: 'root'
})
export class AuthService {

  isLogin: boolean;

  constructor(
    private router: Router,
    private cookie: CookiesService,
    private loading: LoadingActionService
  ) { }

  login(data: any): Observable<any> {
    const getUser = localStorage.getItem('user');
    if (getUser) {
      const parseUser: any[] = JSON.parse(getUser);
      const checkLogin = parseUser.some(val => val.email === data.email && val.password === data.password);
      if (checkLogin) {
        this.loading.open();
        setTimeout(() => {
          this.cookie.setCookie('login', '1', {minute: 30});
          this.router.navigate(['/account']);
          this.isLogin = true;
          this.loading.off();
        }, 1000);
      }
    } else {

    }
    return of(true);
  }

  logout(): void {
    this.loading.open();
    setTimeout(() => {
      this.isLogin = false;
      this.cookie.deleteCookie('login');
      // this.router.navigate(['/']);
      this.loading.off();
    }, 1000);
  }

  checkLogged(): void {
    this.isLogin = !!this.cookie.getCookie('login');
  }

}
