import { Injectable } from '@angular/core';
import { CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot, CanActivateChild, Router, CanDeactivate } from '@angular/router';
import { Observable } from 'rxjs';
import { ProfileComponent } from '../account/profile/profile.component';

@Injectable({
  providedIn: 'root'
})
export class UserCanDeactivate implements CanDeactivate<ProfileComponent> {
  constructor() {}

  canDeactivate(
    component: ProfileComponent,
    currentRoute: ActivatedRouteSnapshot,
    currentState: RouterStateSnapshot,
    nextState: RouterStateSnapshot
  ): Observable<boolean>|Promise<boolean>|boolean {
    console.log(component);
    if (component.typing) {
      return confirm('Your data will lose if you change page!');
    }
    return true;
  }
}
