import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { HomeComponent } from './home/home.component';
import { FeaturesComponent } from './features.component';
import { DirectiveComponent } from './directive/directive.component';
import { EventsComponent } from './events/events.component';
import { EventDetailComponent  } from './events/event-detail/event-detail.component';
import { LifecycleHooksComponent } from './lifecycle-hooks/lifecycle-hooks.component';
import { FormTrainComponent } from './form-train/form-train.component';
import { FormReactiveComponent } from './form-train/form-reactive/form-reactive.component';
import { FormTemplateDrivenComponent } from './form-train/form-template-driven/form-template-driven.component';

const routes: Routes = [
  {
    path: '',
    component: FeaturesComponent,
    children: [
      {
        path: '',
        component: HomeComponent,
      },
      {
        path: 'directive',
        component: DirectiveComponent,
      },
      {
        path: 'events',
        component: EventsComponent
      },
      {
        path: 'events/:id',
        component: EventDetailComponent
      },
      {
        path: 'lifecycle-hooks',
        component: LifecycleHooksComponent
      },
      {
        path: 'form',
        loadChildren: () => import('./form-train/form-train.module').then(m => m.FormTrainModule),
        // data: { preload: true }
      },
      // {
      //   path: 'form/form-reactive',
      //   component: FormReactiveComponent
      // },
      // {
      //   path: 'form/form-template',
      //   component: FormTemplateDrivenComponent
      // }
      {
        path: 'n-form',
        loadChildren: () => import('./n-form/n-form.module').then(m => m.NFormModule)
      },
      {
        path: 'learn-form',
        loadChildren: () => import('./learn-form/learn-form.module').then(m => m.LearnFormModule)
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class FeaturesRoutingModule { }
