import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class CookiesService {

  constructor() { }

  setCookie(name: string, value: string, time: any = {day: 0, minute: 0, always: false}) {
    let expires;
    const date = new Date();
    if (time.day) {
      date.setTime(date.getTime() + (+time.day * 24 * 60 * 60 * 1000));
      expires = '; expires=' + (<any>date).toGMTString();
    } else if (time.minute) {
      date.setTime(date.getTime() + (+time.minute * 60 * 1000));
      expires = '; expires=' + (<any>date).toGMTString();
    } else if (time.always) {
      expires = '; expires=Fri, 31 Dec 9999 23:59:59 GMT';
    } else {
      expires = '';
    }
    document.cookie = name + '=' + value + expires + '; path=/';
  }

  getCookie(name: string) {
    const names = name + '=';
    const ca = document.cookie.split(';');
    for (let i = 0; i < ca.length; i++) {
      let c = ca[i];
      while (c.charAt(0) === ' ') {
        c = c.substring(1);
      }
      if (c.indexOf(names) === 0) {
        return c.substring(names.length, c.length);
      }
    }
    return '';
  }

  deleteCookie(name: string, path = '/', domain = '') {
    document.cookie = name + '=;'
      + 'path=' + path + ';'
      + 'domain=' + domain + ';'
      + 'expires=Thu, 01-Jan-70 00:00:01 GMT';
  }

}
