import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, FormControlName, FormBuilder, Validators } from '@angular/forms';
import { LoadingActionService } from './../../core/services/loading-action.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.scss']
})
export class RegisterComponent implements OnInit {

  registerForm: FormGroup;

  constructor(
    private fb: FormBuilder,
    private loading: LoadingActionService,
    private router: Router
  ) { }

  ngOnInit() {
    this.registerForm = this.fb.group({
      firstName: ['', Validators.maxLength(10)],
      lastName: [''],
      email: [''],
      password: [''],
      confirmPassword: ['']
    });
  }

  submit() {
    const user = localStorage.getItem('user');
    if (user) {
      const parseUser: any[] = JSON.parse(user);
      const checkExist = parseUser.some(val => val.firstName === this.registerForm.value.firstName);
      if (!checkExist) {
        parseUser.push(this.registerForm.value);
        localStorage.setItem('user', JSON.stringify(parseUser));
        this.loading.open();
        setTimeout(() => {
          this.loading.off();
          this.router.navigate(['/auth/login']);
        }, 1000);
      } else {
        alert('First name is existed, Please input another name.');
      }
    } else {
      localStorage.setItem('user', JSON.stringify([this.registerForm.value]));
    }
  }

}
